#contact {
  width: 100%;
  height: 330px;
  display: flex;
  background-color: #0c397c;
  position: relative;
}

.pattern {
  width: 100%;
  background-image: url('../../assests/img/pattern1.png'); /* Substitua pelo caminho da sua imagem */
  background-repeat: repeat;
  background-size: 450px;
  opacity: 0.1;
  background-position: center;
  position: absolute;
  z-index: 9;
  height: 100%;
}

#contact p {
  font-size: large;
  font-weight: 400;
  color: white;
  opacity: 1;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  margin: 0;
  z-index: 10;
}
