@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400&display=swap');

.section-about-me {
    background-color: #441c3285;
    height: 110vh;
    width: auto;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container-about-me {
    background: url("../../assests/img/rosto-big-background.webp");
    background-size: 115%;
    display: flex;
    background-color: #17111f;
    height: 80vh;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    width: 80vw;
    border: 11px solid #ffff004f;
    border-radius: 120px 20px;
    animation: movinBackground 20s ease-in-out alternate-reverse infinite;
}

.photo img[alt="Rosto Cortado do Thiago"] {
    border-radius: 15px;
}

.photo {
    /* width: 30vw; */
    /* z-index: 10; */
    background-position: center;
    width: 50%;
    /* background-color: #3c1156a1; */
    height: 100%;
    background-repeat: no-repeat;
    background-size: contain;
    /* animation: opacity 3s ease-in-out infinite; */
    position: relative;
}

.photo img[alt='Cabelo e Roupa Cortado do Thiago'] {
    position: absolute;
    /* background: #66339970; */
    z-index: 8;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -100%);
    /* height: 110px; */
    width: 100%;
}

.photo img[alt="Rosto Cortado do Thiago"] {
    position: absolute;
    /* background: #ff000057; */
    z-index: 9;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -100%);
    opacity: 0.85;
    /* height: 110px; */
    width: 100%;
}

.text-about-me {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    padding: 0 3vw;
    color: aliceblue;
    border-radius: 35px 10px 108px 30px;
    background: linear-gradient(45deg, #9920dc4f, #2f2440a3, #e73c7e7a, #120a13bd);
    background-size: 300%;
    animation: gradient 20s ease-in-out infinite;
    /* animation: gradient 20s ease-in-out infinite; */
}

.text-about-me h1 {
    color: #ffff0091;
    margin: 10px 0;
    text-decoration: underline;
    font-size: 3.5vw;
}

.text-about-me p {
    line-height: 2rem;
    font-size: 1.2vw;
    text-align: justify;
    margin: 0;
}

@keyframes movinBackground {
	0% {
		background-position: 0% 50%;
	}
	100% {
		background-position: 100% 0%;
	}
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

