header {
  top: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 70px;
  background-color: #0c397cde;
  display: flex;
  color: white;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 11;
  backdrop-filter: blur(10px);
}

header nav {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

header ul {
  list-style: none;
  display: flex;
  gap: 30px;
}

header li {
  cursor: pointer;
  width: 85px;
  text-align: center;
}

header a { font-weight: 600; }

header span {
  align-items: center;
  color: #0b7add;
  display: flex;
  font-size: large;
  font-weight: 700;
  justify-content: center;
  text-shadow: 2px 2px #09306b;
  background: #07214a3d;
  height: -moz-fit-content;
  border-radius: 20px;
  width: 5vw;
}

span img[alt="Imagem de Olho"] {
  width: 30px;
  margin-right: 10px;
  height: 30px;
}

.container-logo {
  position: relative;
  width: 5vw;
}

img[alt='logo-fundo'] {
  pointer-events: none;
  animation: App-logo-spin infinite 20s linear;
  width: 100%;
  height: 100%;
}

img[alt='log'] {
  width: 2.2vw;
  position: absolute;
  top: 50%;
  left: 50%;
  cursor: pointer;
  transform: translate(-50%, -50%);
}

li a {
  padding: 20px 0px;
  display: inline-block;
  position: relative;
  opacity: 0.8;
}

li a:hover { opacity: 1; }

li a::before {
  transition: 300ms;
  height: 5px;
  content: "";
  position: absolute;
  background-color: #1a6dea;
  border-radius: 5px;
  width: 100%;
  bottom: 5px;
  opacity: 0;
}

li a:hover::before {
  bottom: 10px;
  opacity: 1;
}
