.container-skills {
  display: flex;
  max-width: 100%;
  height: 80vh;
  margin: 0 auto;
  background-color: #FFD454;
  align-items: center;
  flex-direction: column;

  background-image: linear-gradient(149deg, transparent 66%, #0c397c 66.01%), linear-gradient(31deg, #0b336f 34%, transparent 34.01%);
  background-position: 66px 200px;
  background-size: 150px 100%;
  background-repeat: repeat-x;
}

.container-skills span {
  transition: revert;
  color: #fac117;
  font-size: 3rem;
  text-shadow: 2px 2px #c09e39;
  font-weight: 600;
  transition: all 300ms ease-in-out;
}

.text-transition {
  opacity: 1;
  transition: opacity 0.3s ease-in-out; /* Transição de 0.3 segundos com curva de temporização suave */
}

.fade-out {
  opacity: 0;
}

