.container-title {
  display: flex;
  align-content: center;
  width: 560px;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 40px;
}

.container-title h1 {
  color: #e36784;
  text-shadow: 3px 3px 1px #b02b4a;
  font-size: 3.5vw;
  text-align: center;
}

.container-title hr {
  color: #e3678485;
  border: 2px solid;
  width: -webkit-fill-available;
  margin: 0 auto;
  border-radius: 5px;
}

.carousel-wrapper {
  position: relative;
  padding: 15px;
  max-width: 60vw;
  height: 280px;
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.gallery-wrapper {
  overflow-x: hidden;
}

.arrow-left,
.arrow-right {
  position: absolute;
  top: 0;
  left: 0;
  right: auto;
  bottom: 0;
  font-size: 20px;
  line-height: 250px;
  width: 50px;
  color: #fff;
  transition: all 600ms ease-in-out;
  background: linear-gradient(to left, transparent 0%, #e36784 200%);
  opacity: 0.5;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  z-index: 10;
}

.arrow-left:hover,
.arrow-right:hover {
  opacity: 1;
}

.arrow-right {
  right: 0;
  left: auto;
  text-align: right;
  background: linear-gradient(to right, transparent 0%, #e36784 200%);
}

.gallery {
  display: flex;
  flex-flow: row nowrap;
  gap: 15px;
}

