.item {
  width: 200px;
  height: 200px;
  flex-shrink: 0;
  transition: all 600ms ease;
  opacity: 0.5;
  border-radius: 10px;
  margin: 30px 0;
  box-shadow: 5px 5px 5px #00000080;
}

.current-item {
  opacity: 1;
  margin: 0;
  width: 250px;
  height: 250px;
}

.colchetes-left,
.colchetes-right {
  position: absolute;
  left: -200px;
  right: auto;
  height: 50vh;
}

.colchetes-right {
  left: auto;
  right: -200px;
}

